import React, { Component } from 'react';
import { Link } from "react-router-dom";
class Nav extends Component{
    constructor(props){
        super(props)
        this.state ={
            inUse:false,
            data:this.props.data,
        }
        this.navController = this.navController.bind(this);
        this.navClose = this.navClose.bind(this);
    }
    navController(e){
        this.setState({
            inUse: !this.state.inUse,
        });
    }
    navClose(e){
        if(e.target.id!="nav" && this.state.inUse==true){
          this.setState({
            inUse: false,
        });
        }
    }
    componentDidMount(){
        let body = document.querySelector("body");
        body.addEventListener("click", this.navClose);
    }
    render(){
        let data = this.state.data;
        let navs = data.map(nav=><NavSub key={nav.value} href={nav.href} value={nav.value}/>)
        if(!this.state.inUse){
            return <div className="navbar"><p id="nav" onClick={this.navController}>{this.props.value}</p></div>
        }
        if(this.state.inUse=true){
            return  <div className="navbar">
                <p id="nav" onClick={this.navController} >{this.props.value}</p>
                <ul id="navul">
                    {navs}
                </ul>
                </div>
        }
    }
}
class NavSub extends Component{
    constructor(props){
        super(props)
    }
    render(){
        return <li><Link to={this.props.href}>{this.props.value}</Link></li>
    }
}
export default Nav;