import React, { Component } from 'react';
import "../css/chekbox.css";


class ChekBox extends Component{
    constructor(props){
      super(props)
    }
    chekbox(){
        let arrbtn = document.querySelectorAll(".cont span");
        let arrchek = document.querySelectorAll(".topinp");
        
        for(let i = 0; i<arrbtn.length; i++){
            arrbtn[i].addEventListener("click", this.open);
        }
        for(let i = 0; i<arrchek.length; i++){
            arrchek[i].addEventListener("click", this.check);
        }
    }
    check(){
        let parent = this.parentNode.parentNode;
        let boxes = document.querySelectorAll(`#${parent.id} input`);
        if(this.checked == true){
            for(let i = 0; i<boxes.length; i++){
                boxes[i].checked = true;
            }
        }else{
            for(let i = 0; i<boxes.length; i++){
                boxes[i].checked = false;
            }
        }
    }
    
    open(){
        let ul = this.parentNode.lastChild
        let el = this
            if(ul.style.display == "block"){
                ul.style.display = "none"
                el.style.color = "gray"
            }else{
                ul.style.display = "block"
                el.style.color = "black"
            }
    }
    componentDidMount(){
        this.chekbox();
    }
    render(){
        return(
    <div className="cont">
        <div className="chekcont">
                <ul id="tree">
                    <li id="ch0"><label><input className="topinp" type="checkbox"></input>Главный чекбокс</label><span>&#9660;</span>
                    <ul>
                        <li id="ch1"><label><input type="checkbox"></input> Внутренний чекбокс 1</label></li>
                        <li id="ch2"><label><input type="checkbox"></input> Внутренний чекбокс 2</label></li>
                        <li id="ch3"><label><input className="topinp" type="checkbox"></input> Внутренний чекбокс 3</label><span>&#9660;</span>
                            <ul>
                                <li id="ch3.1"><label><input type="checkbox"></input> Внутренний чекбокс 3.1</label></li>
                                <li id="ch3.2"><label><input type="checkbox"></input> Внутренний чекбокс 3.2</label></li>
                                <li id="ch3.3"><label><input type="checkbox"></input> Внутренний чекбокс 3.3</label></li>
                            </ul>
                        </li>
                        <li id="ch4"><label><input type="checkbox"></input> Внутренний чекбокс 4</label></li>
                    </ul>
                    </li>
                </ul>
        </div>
    </div>
        )
    }
}
export default ChekBox;