import React, { Component } from 'react';
import '../css/App.css';
import { HashRouter, Switch, Route, Link} from "react-router-dom";
import Nav from "./Nav";
import About from "./About";
import Stars from "./Stars";
import TickTack from './Ticktack';
import Timer from "./Timer";
import AnnuityCalc from './Annuitycalc';
import SearchForm from "./NativeSearch";
import ToDoList from './NativeToDoList';
import SubSelect from './NativeSubSelect';
import ChekBox from './NativeChekBox';
import NotFound from './NotFound';
class App extends Component{
  constructor(props){
    super(props)
  }
  render(){
    return(
      <HashRouter>
      <div className="app-container">
        <header>
          <nav>
              <p><Link to="/">about</Link></p>
              <Nav value="React &#9660;" data={[{value:"Stars", href:"/stars"}, {value:"Tick Tack Toe", href:"/tick_tack"}, {value:"Timer", href:"/timer"}, {value:"аннуитет", href:"/pay-and-cry"}]}/>
              <Nav value="Native JS &#9660;" data={[{value:"SearchForm", href:"/Search_form"}, {value:"todolist", href:"/to_do_list"}, {value:"Subselect", href:"/sub_select"}, {value:"checkbox", href:"/checkbox"}]}/>
          </nav>
        </header>
        <main>
          <div className="content">
            <Switch>
              <Route exact path="/" component={About}/>
              <Route path="/stars" component={Stars}/>
              <Route path="/tick_tack">
                <div className="game">
                  <TickTack />
                </div>
              </Route>
              <Route path="/timer">
                <div className="timer">
                  <Timer />
                </div>
              </Route>
              <Route path="/pay-and-cry">
              <div className="pay-and-cry">
                <AnnuityCalc/>
              </div>
              </Route>
              <Route path="/Search_form" component={SearchForm}/>
              <Route path="/to_do_list" component={ToDoList}/>
              <Route path="/sub_select" component={SubSelect}/>
              <Route path="/checkbox" component={ChekBox}/>
              <Route path="*" component={NotFound}/>
            </Switch>
          </div>
        </main>
      </div>
      </HashRouter>
    )
  }
}

export default App;
