import React from 'react';
import "../css/timer.css";
function Timer(props){
    let [timeValue, setTimeValue] = React.useState("");
    let [tmp, setTmp] = React.useState("");
    React.useEffect(()=>{
        let timer;
        if(timeValue>0){
            timer = setInterval(()=>setTimeValue(timeValue-1),1000);
        }if(timer)
        return ()=>{
            if (timer){
                clearInterval(timer);
            }
        }
    }, [timeValue]);
    function startTimer(){
        let regExp = /^[1-9]$|^[1-9]\d+$/;
        if(regExp.test(tmp)){
            setTimeValue(tmp*60);
        }else{
            alert("Некорректный ввод");
            setTmp("");
        }
    }
    function reStart(){
        setTmp("");
        setTimeValue("");
    }
    if(timeValue===""){
        return <form>
            <input className="setTimeInput" value={tmp} placeholder="MIN" onChange={(e)=>setTmp(e.target.value)}></input>
            <button onClick={startTimer}>Start</button>
        </form>
    }if(timeValue===0){
        return <div>
            <p>Готово</p>
            <button onClick={reStart}>Restart</button>
        </div>
    }else{
        return <div>
            <p><span>{Math.floor(timeValue/60)}:</span><span>{Math.floor(timeValue%60)}</span></p>
            <button onClick={reStart}>Restart</button>
        </div>
    }
}
export default Timer;