import React, { Component } from 'react';
import "../css/searchform.css";


class SearchForm extends Component{
    constructor(props){
      super(props)
      this.search=this.search.bind(this);
    }
    search(){
        let arrTest = ["авито", "айфон", "ашан", "берёза", "бамбук", "василиск", "вагон", "группа", "газон", "грибы", "за орду", "за императора", "ехал грека через реку", "шла саша по шоссе", "работа не волк", "восславь солнце"];
        let inp = document.querySelector("#inp");
        let res = document.querySelector(".res");
        let bd = document.querySelector("body");
    
        function input(){
            let str = inp.value.toLowerCase();
            let ul = document.querySelector(".res ul");
            ul.innerHTML = ""
            res.style.border = "none";
            if(str == "" || str == " "){
                res.style.display = "none";
                inp.value = null;
            }else{
                res.style.display = "block";
                for(let i=0; i<arrTest.length; i++){
                    if(arrTest[i].startsWith(str) == true){
                        res.style.border = "solid 1px gray";
                        let elem = document.createElement("LI");
                        elem.className = "n";
                        let cont = document.createElement("DIV");
                        cont.className = "n";
                        let close = document.createElement("SPAN");
                        close.className = "n";
                        close.innerHTML = "&#10006;"
                        cont.innerHTML = `${arrTest[i]}`;
                        ul.append(elem);
                        elem.append(cont);
                        elem.append(close);
                        close.addEventListener("click", function(){
                            this.parentNode.remove();
                        })
                        cont.addEventListener("click", function(){
                            inp.value = this.textContent;
                        })
                    }
                }
            }  
        }
    
        bd.addEventListener("click", function(ev){
            if(ev.target.className !== "n"){
                res.style.display = "none";
            }
        })
        inp.addEventListener("keyup", input);
    }
    componentDidMount(){
        this.search();
    }
    render(){
        return(
    <div>
            <div className="searchform">
            <p>Найти</p>
            <form className="inpcontainer">
                <input type="text" id="inp" autoComplete="off"></input>
                <div className="res">
                    <ul>
                    </ul>
                </div>

            </form>


            </div>
            <div className="test">
                <div>Тестовый массив: авито, айфон, ашан, берёза, бамбук, василиск, вагон, группа, газон, грибы, за орду, за императора, ехал грека через реку, шла саша по шоссе, работа не волк, восславь солнце.</div>
            </div>
    </div>
        )
    }
}

export default SearchForm;