import React, { Component } from 'react';
import "../css/subselect.css";


class SubSelect extends Component{
    constructor(props){
      super(props)
    }
    selectTravel(){
        let obj = {
            tr: ["P212", "P123", "P453", "P332"],
            pl:  ["A95", "A54", "A23", "A31"],
            bs: ["B12-23", "B12-44", "B18-21", "B18-34"],
        }
        let main = document.querySelector('#main')
        let sub = document.querySelector('#sub')
    
        function change(){
            sub.innerHTML = "";
            let val = main.value;
            let tmpval = obj[`${val}`]
            for(let i=0; i<tmpval.length; i++){
                let tmp = document.createElement("OPTION");
                tmp.value = i;
                tmp.innerHTML = tmpval[i];
                sub.append(tmp);
            }
        }
    
        main.addEventListener("change", change);
        change();
    }
    componentDidMount(){
        this.selectTravel();
    }
    render(){
        return(
        <div className="select-container">
            <div>
                <p>Транспорт</p>
                <select autoFocus id="main">
                    <option value="tr">Поезд</option>
                    <option value="pl">Самолёт</option>
                    <option value="bs">Автобус</option>
                </select>
            </div>
            <div>
                <p>Маршрут</p>
                <select id="sub">

                </select>
            </div>
        </div>
        )
    }
}
export default SubSelect;